@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

html, body {
    font-family: "Roboto", sans-serif;
    color: black;

}



a {
    text-decoration: none;
}

.redText {
    color: #dc2121;
}

.greenText {
    color: #278e15;
}

.pageMask {
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    max-width: 100%;
}



@media screen and (max-width: 600px) {
	.content {
		margin-top: 150px;
	}
}

@media screen and (max-width: 500px) {
	.content {
		margin-top: 130px;
	}
}

@media screen and (max-width: 1055px) {
    .highHeader {
        margin-top: 290px;
    }
}


.home {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.coinsRanking {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
}

.coinsRankingTitle {
    font-size: 40px;
    padding-top: 40px;
    padding-bottom: 10px;
}

.coinsRankingNavigation {
    float: right;
}

.coinsRankingNavigation ul {
    list-style-type: none;
    display: flex;
    justify-content: right;
}

.coinsRankingNavigation button {
    background-color: white;
    border: 1px solid #efefef;
    color: #2353ac;
    font-size: 12px;
    padding: 8px;
}

.coinsRankingNavigation button:disabled {
    color: black;
    background-color: #efefef;
}

.coinsRankingNavigation .firstButton {
    border-radius: 2px 0px 0px 2px;
}

.coinsRankingNavigation .lastButton {
    border-radius: 0px 2px 2px 0px;
}

.coinsRankingTable {
    border-collapse: collapse;
}

.coinsRankingTable .coinsRankingTableHead tr {
    border-top: 0.2px solid #dbdfe4;
}

.coinsRankingTable tr {

    border-bottom: 0.2px solid #dbdfe4;
}

.coinsRankingTable td, .coinsRankingTable th {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 10px;
    padding-left: 10px;
    text-align: center;
    white-space: nowrap;
}

.coinsRankingTable td {
    
}

.coinsRankingTable .nameCell {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.coinsRankingTable .nameCell a {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.coinsRankingTable .nameCell a > * {
    display: inline;
}

.coinsRankingTable .logoContainer {
    display: inline-flex;
    width: 30px;
    height: 30px;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    background-size: 100% !important;
}

.coinsRankingTable .coinLogo {
    max-width: 100%;
    max-height: 100%;
}

.searchResultsHeader {
    float: left;
    font-weight: normal;
}

.coinsRankingNavigationContainer {
    width: 100%;
}

@media screen and (max-width: 1055px) {
    .coinsRankingTable {
        width: 100%;
        border-collapse: collapse;
        border-spacing: 0;
    }

    .coinsRankingTitle {
        text-align: center;
    }

    .tableContainer {
        width: 120%;
        overflow-x: auto;
    }
}
.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        -webkit-animation: App-logo-spin infinite 20s linear;
                animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.adminDashboardTitle {
    text-align: center;
    font-size: 48px;
    padding-right: 30px;
}

.allFiltersContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

}


label {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 10px;
}

input {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.history {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.timeframeTitle {
    font-size: 40px;;
}

.filtersSection {

}

.updateFiltersButton {
    margin-top: 20px;
    float:right;
}

.container {
    display: flex;
    flex-direction: column;
    margin-left: 10%;
    margin-right: 10%;
}

.alertsTable {
    width: 100%;
    border:none;
    border-collapse: collapse;
}

td, th {
    text-align: center;
    border: 1px solid #000;
    padding-top: 10px;
    padding-bottom: 10px;
}



form {
    margin-bottom: 20px;
}

.react-datepicker-wrapper {
    margin-bottom: 20px;
}

.heading {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.notificationsList {
    position:absolute;
    -webkit-transform: translate(100px, 0);
            transform: translate(100px, 0);
    padding: 10px;
    background-color: white;
    border: 1px solid black;
}

.bellIcon {
    cursor: pointer;
}
.mainHeader {
    display: flex;
    align-content: stretch;
    padding: 20px;
    

}

.showShadow {
    box-shadow: 0px 2px 5px #27272780;
    transition: box-shadow 0.3s ease-in-out;

}



.titleContainer {
    flex-basis: 33%;
    flex-grow: 1;
    

    
}

.headerContainer {
    display: flex;
    flex-direction: column;
    z-index: 10;
    position: fixed;
    background-color: white;
    width: 100%;
    top: 0;
    transition: box-shadow 0.2s ease-in-out;
}

.titleContainer a {
    color: black;
    text-decoration: none;
}

.titleContainer .titles {
    display: inline-flex;
    flex-direction: column;
}

.titleContainer .titles img {
    align-self: center;
    width: 33vw;
    margin: 5px;
}

.titleContainer p {
    color: #404040;
    text-align: center;

}

.mainNavigation {
    flex-basis: 33%;
    flex-grow: 1;
    display: flex;
    justify-content: center;
}

.mainNavigation li {
    align-self: center;
    flex-grow: 1;

}
.mainNavigation a {
    padding: 20px;
    text-decoration: none;
    color: black;
    font-weight: bold;
    cursor: pointer;
}


.browses:after{
    content: '';
    border: 4px solid transparent;
    border-top: 4px solid #616161;
    margin-left: 2px;
    margin-bottom: 3px;
    display: inline-block;
    vertical-align: bottom;
}

.mainNavigation ul {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.searchContainer {
    flex-basis: 33%;
    justify-content: center;
    flex-grow: 1;
    display: flex;
}

.searchContainer input {
    width: 80%;
    align-self: center;
    border-color: transparent;
    border-radius: 2px;
    background-color: lightgray;
    padding: 10px;
}

.showNavButton {
    visibility: hidden;
    display: none;
}



@media screen and (max-width: 1055px) {
    .headerContainer {
        flex-direction: row;
        min-height: 130px;
    }

    .hide {
        display: none;
        visibility: hidden;
    }

    .titleContainer {
        display: none;
        visibility: hidden;
        text-align: center;
        font-size: 25px;
        padding-top: 10px;

    }

    .titleContainer img {
        width: 60vw!important;
    }

    .mainHeader {
        width: 100%;
        padding-left: 0;
        padding-right: 50px;
        flex-direction: column;
    }

    .mainNavigation {
        visibility: hidden;
        display: none;
        font-size: 20px;
        flex-direction: column;
        flex-flow: wrap;

    }

    .mainNavigation ul {
        flex-direction: column;
        display: flex;
    }

    .mainNavigation a {
        font-size: 30px;
        
    }

    .mainNavigation li {
        padding-top: 20px;
        padding-bottom: 20px;
    }

    .searchContainer {
        visibility: hidden;
        display: none;
    }

    .showNavButton {
        padding-left: 20px;
        visibility: visible;
        display: inline;
        border: none;
        background-color: white;
        font-size: 30px;
    }

    .show {
        visibility: visible;
        display: inline;
    }

    .show.mainNavigation {
        display: flex;
    }


    

    .showNavButton::before {
        content: "\2630";
    }

    .hideIcon::before {
        content: "x";
    }




}

@media screen and (max-width: 580px) {
    .titleContainer {
        font-size: 15px;
    }
    .headerContainer {
        max-width: 100vw;
    }
}

@media screen and (max-width: 500px) {
    .mainHeader {
        padding-top: 10px;
        padding-bottom: 0px;
        padding-right: 15px;
    }

    .headerContainer {
        min-height: 100px;
    }

    .titleContainer img {
        width: 80vw!important;
    }
}

@media screen and (max-width: 400px) {
    .titleContainer {
        font-size: 15px;
        
    }

    
}
